//基础字体
// @import "font-awesome.min.less";//基础图标字体
//resize
@bodybg:white; //页面背景
@shade_color:rgb(0,0,0);//zz遮罩颜色
@shade_opacity:0.5;//遮罩透明度

//4个基础断点,分为5个区域
@w_sm:575px; //xs最大宽度
@w_md:700px; //sm做大宽度
@w_lg:991px; //md最大宽度 默认
@w_xl:1199px; //lg最大宽度 默认

//节点尺寸
@had_xs:1;//已哪个节点为基础节点,默认xl(0=flase/1=true)
@had_sm:1;
@had_md:1;
@had_lg:1;
@had_xl:0;

@had_sf:0;//自定义节点名(0=true/1=false)
@self_pointname:sf;
@self_pointmin:1699px;
@self_pointmax:1920px;

//连接颜色
@link_color:#666666;
@link_hovercolor:#3ca14d;
@linklight_color:#e543ae;
@linklight_hovercolor:@linklight_color+10;

//自定义图标字体
@eot:iconfont.eot;
@woff:iconfont.woff;
@ttf:iconfont.ttf;
@svg:iconfont.svg;

@font-face {font-family: "iconfont";
  src: url('@{eot}');
  src: url('@{eot}?#iefix') format('embedded-opentype'),
  url('@{woff}') format('woff'),
  url('@{ttf}') format('truetype'),
  url('@{svg}#iconfont') format('svg');}

.o_icofont{
  font-family:"iconfont" !important;
  font-size:16px;
  font-style:normal;
  -webkit-font-smoothing: antialiased;
  -webkit-text-stroke-width: 0.2px;
  -moz-osx-font-smoothing: grayscale;
}

