//下拉菜单
@select_arrowcolor:#333; //箭头颜色
@select_arrowfont:iconfont; //箭头字体
@select_arrowfont_code:"\e655";//箭头字体代码
@select_arrowsize:12px;
@select_bordercolor:#ccc; //边框颜色
@select_height:50px; //高度
@select_p_left:15px; //选项左右间距
@select_color:#666; //默认文字颜色
@select_hoverbgcolor:#ededed; //hover颜色
@select_selectedbgcolor:black; //选中颜色
