@select_losebgcolor:#efefef;//失效背景颜色
@select_losecolor:#999;//失效文字颜色
@select_groupcolor:black;//大类文字颜色

//按钮颜色参数
@btnbase_bgcolor:#eee; //主颜色
@btnbase_color:#999; //文字颜色
@btnbase_hovercolor:#333; //hover颜色

@btn1_bgcolor:#3ca14d; //主颜色
@btn1_color:white; //文字颜色
@btn1_hovercolor:#3ca14d+20; //hover颜色

@btn2_bgcolor:#e543ae; //辅助色
@btn2_color:white; //文字颜色
@btn2_hovercolor:#e543ae+20; //hover颜色

@btn_losebgcolor:#f8f8f8; //失效颜色
@btn_losebordercolor:#eee;//失效边框颜色
@btn_losecolor:#ccc; //失效颜色

//按钮大小参数 分s/df/xl三个尺寸
@btn_s:30px; //高度
@btn_s_padding:15px; //填充
@btn_s_font:12px;//文字大小
@btn_s_icopadding:8px;//图标padding

@btn_df:38px; //高度
@btn_df_padding:20px;
@btn_df_font:14px;//文字大小
@btn_df_icopadding:11px;//图标padding

@btn_big:48px; //高度
@btn_big_padding:45px;
@btn_big_font:16px;//文字大小
@btn_big_icopadding:15px;//图标padding

//form
@form_width:70%;//表单宽度
@input_height:30px; //表单高度
@input_bgcolor:white; //表单颜色
@input_bordercolor:#999; //边框颜色
@input_fsize:14px; //文本框文字大小
@input_color:#333; //文字颜色
@label_fsize:14px; //label文字大小
@row_margin:5px;//行间距

//layout
@row3_r1_height:100px; //三行布局 head 高度
@clientHeight:'document.body.clientHeight'; //三行布局 head 高度
@row3_r3_height:150px;

//scrollbar
@sbar_w:20px;//滚动条盒子宽度
@sbar_linewidth:1px;//线宽度
@sbar_mt:15px;//上下间距
@sbar_linecolor:#eee;//线颜色
@sbar_barcolor:black;
@sbar_barw:5px;//滚动条宽度

//popup 弹层
@popup_df_w:860px;//弹层宽度df为默认
@popup_xl_w:860px;
@popup_lg_w:600px;
@popup_md_w:400px;
@popup_sm_w:320px;
@popup_xs_w:320px;
@popup_sf_w:320px;//自定义节点
@popupclose_w:30px;//关闭按钮大小
@popupclose_bg:#eee;//关闭按钮背景
@popupclose_text:\f00d;
@popupshade_opacity:0.5;//遮罩透明度

@alert_df_w:400px;//alert 提示文字弹层
@alert_xl_w:400px;
@alert_lg_w:400px;
@alert_md_w:400px;
@alert_sm_w:300px;
@alert_xs_w:300px;
@alert_sf_w:300px;//自定义节点
@alert_cont_padding:30px;
@alert_font_size:14px;
@alert_font_color:#333;

@confirm_df_w:500px;//confirm 确认弹层
@confirm_xl_w:500px;
@confirm_lg_w:500px;
